header {
  background-color: #82a6cb;
  grid-template-columns: 2fr repeat(8, 1fr) 2fr;
  place-content: center;
  place-items: center;
  gap: 10px;
  min-width: 700px;
  padding: 10px;
  display: grid;
}

header.dark-mode {
  color: #e0e0e0;
  background-color: #16213e;
}

.titulo.dark-mode {
  color: #e0e0e0;
}

.titulo {
  font-size: 50px;
}

footer {
  background-color: #82a6cb;
  flex-grow: 3;
  grid-template-columns: repeat(9, 1fr) 2fr;
  gap: 10px;
  width: 100%;
  min-width: 700px;
  min-height: 50px;
  max-height: 50px;
  display: grid;
  position: fixed;
  bottom: 0;
}

footer.dark-mode {
  background-color: #1a1a2e;
}

.contacto.dark-mode {
  color: #e0e0e0;
}

.contacto {
  grid-column: 10;
  justify-content: end;
  justify-items: center;
}

html, body {
  background-color: #bdd8f1;
  flex-direction: column;
  min-width: fit-content;
  min-height: 100vh;
  margin: 0;
  display: flex;
}

body.dark-mode, html.dark-mode {
  color: #e0e0e0;
  background-color: #0f3460;
}

button.modo {
  cursor: pointer;
  background-color: #3667a6;
  grid-area: 1 / 12 / 2 / 13;
}

button.dark-mode {
  background-color: #0f3460;
}

a {
  color: #000;
  font-size: 32px;
  text-decoration: none;
}

p {
  color: #000;
  margin: 0;
  font-size: 32px;
  text-decoration: none;
}

main {
  grid-template-rows: repeat(11, 60px);
  grid-template-columns: repeat(12, 1fr);
  gap: 10px;
  margin: 0 auto;
  padding: 10px;
  display: grid;
}

.iniciar_sesion {
  grid-column: 10;
  justify-content: end;
  justify-items: center;
}

div.contacto {
  background-color: #82a6cb;
  border-radius: 30px;
  grid-area: 2 / 4 / 11 / 10;
  grid-template-rows: repeat(10, 1fr);
  grid-template-columns: repeat(6, 1fr);
  place-content: center;
  place-items: center;
  min-width: 700px;
  min-height: 100%;
  display: grid;
}

div.contacto.dark-mode {
  background-color: #16213e;
}

div h1.contacto {
  unicode-bidi: isolate;
  grid-area: 2 / 3 / 3 / 5;
  margin-block: auto;
  margin-inline: auto;
  font-size: 48px;
  font-weight: normal;
}

label.email {
  grid-area: 3 / 2 / 5 / 6;
  width: 100%;
}

div h2.text_email {
  unicode-bidi: isolate;
  width: 100%;
  margin-block: auto;
  margin-inline: auto;
  font-size: 32px;
  font-weight: normal;
}

input.email {
  width: 100%;
  height: 40%;
}

label.nombre {
  grid-area: 5 / 2 / 7 / 6;
  width: 100%;
}

div h2.nombre {
  unicode-bidi: isolate;
  grid-area: 5 / 2 / 6 / 3;
  width: 100%;
  margin-block: auto;
  margin-inline: auto;
  font-size: 32px;
  font-weight: normal;
}

input.nombre {
  grid-area: 6 / 2 / 7 / 6;
  width: 100%;
  height: 40%;
}

label.mensaje {
  grid-area: 7 / 2 / 9 / 6;
  width: 100%;
  height: 100%;
}

div h2.mensaje {
  unicode-bidi: isolate;
  grid-area: 7 / 2 / 8 / 3;
  width: 100%;
  margin-block: auto;
  margin-inline: auto;
  font-size: 32px;
  font-weight: normal;
}

textarea.mensaje {
  grid-area: 8 / 2 / 9 / 6;
  width: 100%;
  height: 50%;
}

button {
  color: #fff;
  background-color: #3667a6;
  border: none;
  grid-area: 9 / 5 / 10 / 6;
  width: 100%;
  font-size: 24px;
}

a.crear_cuenta {
  text-align: center;
  grid-area: 13 / 3 / 14 / 5;
  width: 100%;
}

a.dark-mode {
  color: #e0e0e0;
}

@media only screen and (width <= 1000px) {
  input {
    font-size: 24px;
  }

  main {
    justify-content: center;
    align-items: center;
    gap: 10px;
    min-width: 700px;
    min-height: 900px;
    margin-bottom: 7%;
    padding: 10px;
    display: flex;
  }

  div.contacto {
    background-color: #82a6cb;
    border-radius: 30px;
    grid-template-rows: repeat(10, 1fr);
    grid-template-columns: repeat(6, 1fr);
    place-content: center;
    place-items: center;
    display: grid;
    position: absolute;
    top: 20%;
  }

  div p.contacto {
    grid-area: 2 / 3 / 3 / 5;
    font-size: 64px;
  }

  div p.text_email {
    grid-area: 3 / 2 / 4 / 3;
    width: 100%;
    font-size: 40px;
  }

  input.email {
    grid-area: 4 / 2 / 5 / 6;
    width: 100%;
    height: 40%;
  }

  div p.nombre {
    grid-area: 5 / 2 / 6 / 3;
    width: 100%;
    font-size: 40px;
  }

  input.nombre {
    grid-area: 6 / 2 / 7 / 6;
    width: 100%;
    height: 40%;
  }

  div p.mensaje {
    grid-area: 7 / 2 / 8 / 3;
    width: 100%;
    font-size: 40px;
  }

  textarea.mensaje {
    grid-area: 8 / 2 / 9 / 6;
    width: 100%;
    height: 50%;
    font-size: 24px;
  }

  button {
    color: #fff;
    background-color: #3667a6;
    border: none;
    grid-area: 9 / 5 / 10 / 6;
    width: 100%;
    font-size: 24px;
  }
}
